<template>
  <div>
    <p class="control has-icons-left">
      <input
        v-model="searchBarValue"
        @input="searchTabs"
        @keydown.enter="onEnter"
        type="text"
        id="tabs_searchbox"
        autofocus
        class="input"
        placeholder="Search"
      />
      <span class="icon is-left">
        <i class="fas fa-search" aria-hidden="true"></i>
      </span>
    </p>
  </div>
</template>

<script>
import Fuse from "fuse.js";
import { bus } from "@/popup/main.js";
export default {
  name: "SearchBox",
  methods: {
    searchTabs() {
      bus.$emit("search-tabs", this.searchBarValue);
    },
    onEnter() {
      bus.$emit("enter-click");
    }
  },
  data: () => {
    return {
      searchBarValue: ""
    };
  }
};
</script>

<style scoped></style>
