<template>
  <div>
    <Panel :tabs="tabs" />

    <!-- <div id="tab-list" :key="tab.id" v-for="tab in tabs">
      <img height="20" :src="tab.favIconUrl" />
      <div @click="tabOpen(tab)">{{ tab.title }}</div>
    </div> -->
  </div>
</template>

<script>
import Panel from "@/components/Panel.vue";
import { bus } from "@/popup/main.js";
import Fuse from "fuse.js";
export default {
  name: "App",
  components: { Panel },
  created() {
    bus.$on("search-tabs", searchBarValue => {
      this.searchTabs(searchBarValue);
    });
    bus.$on("open-tab", tab => {
      this.tabOpen(tab);
    });

    this.searchTabs("");
  },
  methods: {
    searchTabs(searchBarValue = "") {
      const owner = this;
      var openTabs = [];
      chrome.windows.getAll({ populate: true }, function(windows) {
        windows.forEach(function(window) {
          window.tabs.forEach(function(tab) {
            //collect all of the urls here, I will just log them instead

            tab.url = tab.url.replace(
              RegExp("(http://|https://)(www.)?", "i"),
              ""
            );

            openTabs.push(tab);
          });
        });

        if (searchBarValue != "") {
          var options = {
            shouldSort: true,
            threshold: 0.7,
            location: 0,
            distance: 200,
            keys: ["url", "title"]
          };

          var fuse = new Fuse(openTabs, options);

          var result = fuse.search(searchBarValue);
        } else {
          var result = openTabs;
        }

        bus.$emit("tabs-to-list", result);
      });
    },
    tabOpen(tab = null) {
      chrome.tabs.getSelected(function(selected) {
        chrome.tabs.update(selected.id, { highlighted: false });
      });
      chrome.windows.update(tab.windowId, { focused: true });
      chrome.tabs.update(tab.id, { highlighted: true });
    }
  },
  data() {
    return {
      tabs: []
    };
  }
};
</script>
<style src="../../node_modules/bulma/css/bulma.css"></style>
<style src="../assets/css/all.css"></style>
<style>
html,
body {
  height: 400px;
  width: 400px;
}
</style>
