<template>
  <div>
    <div
      @mouseenter="mouseEnterItem"
      @click="openTab(tab)"
      :key="tab.id"
      v-for="tab in tabs"
    >
      <a class="panel-block">
        <img class="panel-icon" :src="tab.favIconUrl" />

        <p>{{ tab.title }}</p>
      </a>
    </div>
    <div class="no-match" v-if="tabs.length === 0">
      No Matching Tabs.
    </div>
  </div>
</template>

<script>
import { bus } from "@/popup/main.js";
export default {
  name: "List",
  created() {
    const owner = this;
    bus.$on("tabs-to-list", function(tabs) {
      owner.tabs = tabs;
    });
    bus.$on("enter-click", function() {
      if (owner.tabs.length > 0) {
        bus.$emit("open-tab", owner.tabs[0]);
      }
    });
  },
  methods: {
    openTab(tab) {
      bus.$emit("open-tab", tab);
    },
    mouseEnterItem(e) {
      let siblings = e.target.parentNode.children;

      for (let i = 0; i < siblings.length; i++) {
        if (e.target === siblings[i]) {
          this.selectedIndex = i;
        }
      }
    }
  },
  data() {
    return {
      tabs: [],
      selectedIndex: 0
    };
  }
};
</script>

<style scoped>
.no-match {
  padding: 8px;
  text-align: center;
}
.selected {
  background: #f5f5f5;
}
</style>
